import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { InputSearchProps } from 'summer/dist/views/components/inputSearch/InputSearch';
import { CLASS_NAME } from '../../../../models/dtos/mcg/customizacaoTermo';
import {
  ABERTURA_ENCERRAMENTO,
  BALANCO_PATRIMONIAL,
  CARTA_RESPONSABILIDADE,
  DEMONSTRCAO_RESULTADO,
  VERACIDADE,
} from '../../../../models/enumerated/mcg/customizacaoTermoEnum';

export interface CustomizacaoTermoSearchViewProps extends InputProps {
  apenasCartaResponsabilidade?: boolean;
  apenasIndividualizadaVeracidade?: boolean;
  apenasDemonstracaoResultado?: boolean;
  apenasAberturaEncerramento?: boolean;
  apenasBalancoPatrimonial?: boolean;
}

const CustomizacaoTermoIS: FC<CustomizacaoTermoSearchViewProps> = props => {
  function getSearchExpression(): SearchExpression[] {
    if (props.apenasCartaResponsabilidade) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: CARTA_RESPONSABILIDADE.value,
        },
      ];
    }
    if (props.apenasIndividualizadaVeracidade) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: VERACIDADE.value,
        },
      ];
    }
    if (props.apenasDemonstracaoResultado) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: DEMONSTRCAO_RESULTADO.value,
        },
      ];
    }
    if (props.apenasAberturaEncerramento) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: ABERTURA_ENCERRAMENTO.value,
        },
      ];
    }
    if (props.apenasBalancoPatrimonial) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: BALANCO_PATRIMONIAL.value,
        },
      ];
    }
    return [];
  }

  const inputSearchProps: InputSearchProps<any> = {
    ...props.inputSearchProps,
    columns: [
      {
        dataField: 'descricao',
        text: 'Descrição',
      },
      {
        dataField: 'termo',
        text: 'Termo',
      },
    ],
    dtoClassName: CLASS_NAME,
    searchExpressions: getSearchExpression,
    searchViewId: 'CustomizacaoTermoSearchView',
  };

  return <InputGroupInline {...props} type={InputType.INPUT_SEARCH} inputSearchProps={inputSearchProps} />;
};

export default CustomizacaoTermoIS;
