import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { Col, Row } from 'reactstrap';
import { Box, Checkbox, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../components/mcg/customizacaoTermoIS/CustomizacaoTermoIS';
import ReportFormView from '../../../report/ReportFormView';
import { CLASS_KEY, DeclaracaoVeracidadeRV, REPORT_EVENT, REPORT_TITLE } from './declaracaoVeracidadeRV';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY),
  module: 'MCG',
};

export default function DeclaracaoVeracidadeReportView(props: WindowComponentProps) {
  const history = useHistory();
  const formProps = useForm<DeclaracaoVeracidadeRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      setTimeout(() => {
        const {
          empresa: { id },
        } = globalReducer.globalParameter;
        formProps.register({ name: 'idDeclaracaoVeracidade' });
        formProps.register({ name: 'idGerEmpresa' });
        formProps.setValue('idGerEmpresa', id, true);
        formProps.setValue('contabilista', true, true);
        formProps.setValue('responsavelLegal', true, true);
      }, 800);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().contabilista && formProps.getValues().termo && !formProps.getValues().termo.termoAbertura) {
      erros.push(
        'O termo de declaração de veracidade selecionado não possui configuração para CONTABILISTA e por isso não é ' +
          'possível gerar a declaração para o mesmo. Para utilizar esse termo, desmarque a opção de "Declaração do Contabilista" ou acesse: \n' +
          'Manutenção > Tabelas > Customizações de Termos e Documentos e configure a aba CONTABILISTA do termo em questão.'
      );
    }

    if (formProps.getValues().responsavelLegal && formProps.getValues().termo && !formProps.getValues().termo.termoEncerramento) {
      erros.push(
        'O termo de declaração de veracidade selecionado não possui configuração para RESPONSÁVEL e por isso não é ' +
          'possível gerar a declaração para o mesmo. Para utilizar esse termo, desmarque a opção de "Declaração do Responsável" ou acesse: \n' +
          'Manutenção > Tabelas > Customizações de Termos e Documentos e configure a aba RESPONSÁVEL LEGAL do termo em questão.'
      );
    }

    if (!formProps.getValues().contabilista && !formProps.getValues().responsavelLegal) {
      erros.push('Nenhum tipo de declaração (Contabilista/Responsável) foi selecionada.');
    }

    return erros;
  };

  const setIdDeclaracaoVeracidade = () => {
    if (formProps.getValues().termo) {
      formProps.setValue('idDeclaracaoVeracidade', formProps.getValues().termo.id, true);
    } else {
      formProps.setValue('idDeclaracaoVeracidade', null, true);
    }
  };

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiId={6336}
    >
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history} validateExtras={validateExtras}>
        <Row>
          <Col>
            <CustomizacaoTermoIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Declaração de Veracidade"
              searchViewProps={{ apenasIndividualizadaVeracidade: true }}
              apenasIndividualizadaVeracidade={true}
              placeholder="Selecione a Declaração de Veracidade"
              id="termo"
              onChange={setIdDeclaracaoVeracidade}
              isPrependLabel={true}
              inputSearchProps={{ bsSize: 'xl' }}
            />
          </Col>
        </Row>
        <Box title="Declarações">
          <Row>
            <Col style={{ marginBottom: '10px' }}>
              <Checkbox formProps={formProps} id="contabilista" label="Declaração do Contabilista" />
            </Col>
          </Row>
          <Row>
            <Col style={{ marginBottom: '10px' }}>
              <Checkbox formProps={formProps} id="responsavelLegal" label="Declaração do Responsável" />
            </Col>
          </Row>
        </Box>
      </ReportFormView>
    </WindowComponent>
  );
}
